export function hasClass(el, className) {
    let reg = new RegExp('(^|\\s)' + className + '(\\s|$)')//className 
    return reg.test(el.className)
}


export function addClass(el, className) {
    if (hasClass(el, className)) {//
        return
    }

    let newClass = el.className.split(' ')  //split() className
    newClass.push(className)//className 
    el.className = newClass.join(' ')//join() class
}

export function removeClass(el, className) {
    //class
    if (!hasClass(el, className)) {
        return
    }

    let newClass = el.className.split(' ')     //classclass
    let index = newClass.findIndex((item) => {   //classclass
        return item === className
    })
    newClass.splice(index, 1)   // 
    el.className = newClass.join(' ')  //classclass
}

